.zolo-post-carousel {
    --zolo-meta-box-gap: clamp(0.438rem, 1vw, 0.75rem);
    --zolo-meta-box-padding: clamp(1.25rem, 2vw, 1.875rem);
    --zolo-author-name-color: #fff;
    --zolo-meta-box-img-size: clamp(2.5rem, 4vw, 3.125rem);
    --zolo-link-btn-margin: 20px 0 0;
    --zolo-dateTime-margin: 10px 0 0;
    --zolo-dateTime-gap: clamp(0.5rem, 1vw, 0.625rem);
    --zolo-title-margin: clamp(0.75rem, 1vw, 0.938rem) 0 0;
    --zolo-desc-margin: 10px 0 0;
    --zolo-desc-color: #656566;
    --zolo-category-wrap-gap: 10px;
    --zolo-category-padding: clamp(0.313rem, 0.8vw, 0.438rem) clamp(0.75rem, 1.5vw, 0.875rem);
    --zolo-category-hover-color: #fff;
    --zolo-link-btn-padding: 10px 14px;
    --zolo-link-btn-gap: 10px;
    --zolo-link-btn-color: #494c56;
    --zolo-link-btn-icon-size: 14px;
    --zolo-img-height: clamp(21.875rem, 25vw, 18.75rem);
    --zolo-author-name-color: #fff;
    --zolo-dateTime-color: #36384a;
    --zolo-content-padding: 25px 0 0;
    --zolo-title-color: #111013;
    --zolo-sDateTime-color: #000;
    --zolo-category-color: #000;
    --zolo-pagination-gap: 12px;
    --zolo-pagination-size: 8px;
    --zolo-pagination-border-width: 1px;
    --zolo-pagination-border-style: solid;
    --zolo-pagination-border-color: var(--zoloblocks-brand-color);
    --zolo-nav-button-color: #000;
    --zolo-nav-button-border-radius: 50%;
    --zolo-nav-button-size: clamp(2.188rem, 6vw, 3.125rem);
    --zolo-nav-button-right: 20px;
    --zolo-nav-button-left: 20px;
    --zolo-nav-button-icon-size: 16px;
    position: relative;
    box-sizing: border-box;

    .swiper {
        --review-carousel-mask-offset: 300px;
    }

    .zolo-post-item {
        position: relative;
        overflow: hidden;
        box-sizing: border-box;
    }

    .zolo-post-image {
        position: relative;
        overflow: hidden;
        border-radius: var(--zolo-item-border-radius);
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            vertical-align: middle;
            transition: all cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
        }
    }

    .zolo-post-meta-box {
        position: absolute;
        left: 0;
        right: 0;
        z-index: 1;
        display: inline-flex;
        flex-wrap: wrap;
        align-items: center;
        gap: var(--zolo-meta-box-gap);
        padding: var(--zolo-meta-box-padding);
        background: linear-gradient(360deg, rgba(17, 16, 19, 0.721) 0%, rgba(17, 16, 19, 0) 100%);
        transition: all cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
    }

    .zolo-post-author-name {
        display: inline-flex;
        flex-direction: column;
        font-size: clamp(0.75rem, 1.5vw, 0.875rem);
        text-transform: capitalize;
    }

    .zolo-post-author-name a {
        font-weight: 600;
        text-decoration: none;
        color: var(--zolo-author-name-color);
        transition: color cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
    }

    .zolo-post-meta-box img {
        width: var(--zolo-meta-box-img-size);
        height: var(--zolo-meta-box-img-size);
        border-radius: 50%;
        display: block;
    }

    .zolo-post-link-btn {
        margin: var(--zolo-link-btn-margin);
        .zolo__display-icon {
            display: inline-flex;
            align-items: center;
        }
    }

    .zolo-post-dateTime {
        margin: var(--zolo-dateTime-margin);
    }

    .zolo-post-dateTime,
    .zolo-post-secount-dateTime {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: var(--zolo-dateTime-gap);
        font-size: clamp(0.75rem, 1.5vw, 0.875rem);
    }

    .zolo-post-title {
        margin: var(--zolo-title-margin);
        font-size: clamp(1.125rem, 2.5vw, 1.375rem);
        font-weight: 700;
    }

    .zolo-post-title a {
        text-decoration: none;
        transition: color cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
    }

    .zolo-post-title a:hover,
    .zolo-post-author-name a:hover {
        color: var(--zoloblocks-brand-color-hover);
    }

    .zolo-post-desc {
        margin: var(--zolo-desc-margin);
        p {
            margin: 0;
            color: var(--zolo-desc-color);
            font-size: clamp(0.75rem, 1.5vw, 0.875rem);
        }
    }

    .zolo-post-category {
        list-style: none;
        margin: 0;
        padding: 0;
        display: inline-flex;
        align-items: center;
        gap: var(--zolo-category-wrap-gap);
    }

    .zolo-post-category a {
        font-size: clamp(0.625rem, 1vw, 0.75rem);
        padding: clamp(0.313rem, 0.8vw, 0.438rem) clamp(0.75rem, 1.5vw, 0.875rem);
        display: inline;
        font-weight: 300;
        border-radius: var(--zoloblocks-button-border-radius);
        text-decoration: none;
        text-transform: capitalize;
        transition:
            color cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s,
            background-color cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
    }

    .zolo-post-category a:hover {
        color: var(--zolo-category-hover-color);
        background-color: var(--zoloblocks-brand-color-hover);
    }

    .zolo-post-link-btn a {
        font-size: clamp(0.75rem, 1.5vw, 0.875rem);
        text-transform: capitalize;
        color: var(--zolo-link-btn-color);
        text-decoration: none;
        display: inline-flex;
        align-items: center;
        gap: var(--zolo-link-btn-gap);
        padding: var(--zolo-link-btn-padding);
        border-radius: var(--zoloblocks-button-border-radius);
        transition: all cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
        justify-content: center;
    }

    .zolo-post-link-btn a:hover {
        color: var(--zoloblocks-brand-color-hover);
    }

    .zolo-post-link-btn a svg,
    .zolo-post-link-btn a i {
        width: var(--zolo-link-btn-icon-size);
        height: var(--zolo-link-btn-icon-size);
        font-size: 14px;
    }

    &.zolo-post-style-1 .zolo-post-image,
    &.zolo-post-style-2 .zolo-post-image,
    &.zolo-post-style-3 .zolo-post-image {
        height: var(--zolo-img-height);
    }

    /* global css end  */
    &.zolo-post-style-1 .zolo-post-meta-box {
        bottom: 0;
        opacity: 0;
        transform: translateY(6%);
    }

    &.zolo-post-style-1 .zolo-post-item:hover .zolo-post-meta-box,
    &.zolo-post-style-2 .zolo-post-item:hover .zolo-post-meta-box {
        transform: none;
        opacity: 1;
    }

    &.zolo-post-style-1 .zolo-post-author-name,
    &.zolo-post-style-2 .zolo-post-author-name,
    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-author-name {
        color: var(--zolo-author-name-color);
    }

    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-author-name {
        color: #fff;
    }

    &.zolo-post-style-1 .zolo-post-item:hover .wp-post-image,
    &.zolo-post-style-2 .zolo-post-item:hover .wp-post-image,
    &.zolo-post-style-3 .zolo-post-item:hover .wp-post-image {
        transform: scale(103.5%);
    }

    &.zolo-post-style-1 .zolo-post-dateTime {
        color: var(--zolo-dateTime-color);
    }

    &.zolo-post-style-1 .zolo-post-content {
        padding: var(--zolo-content-padding);
    }

    &.zolo-post-style-1 .zolo-post-title a,
    &.zolo-post-style-3 .zolo-post-title a,
    &.zolo-post-style-4 .zolo-post-title a {
        color: var(--zolo-title-color);
    }

    &.zolo-post-style-1 .zolo-post-link-btn a,
    &.zolo-post-style-2 .zolo-post-link-btn a,
    &.zolo-post-style-3 .zolo-post-link-btn a,
    &.zolo-post-style-1 .zolo-post-secount-dateTime,
    &.zolo-post-style-2 .zolo-post-secount-dateTime,
    &.zolo-post-style-3 .zolo-post-secount-dateTime,
    &.zolo-post-style-1 .zolo-post-category a {
        background-color: #ecf2fe;
    }

    &.zolo-post-style-1 .zolo-post-link-btn a,
    &.zolo-post-style-2 .zolo-post-link-btn a,
    &.zolo-post-style-3 .zolo-post-link-btn a {
        --zolo-link-btn-color: #000;
        color: var(--zolo-link-btn-color);
    }

    &.zolo-post-style-1 .zolo-post-secount-dateTime,
    &.zolo-post-style-2 .zolo-post-secount-dateTime,
    &.zolo-post-style-3 .zolo-post-secount-dateTime {
        color: var(--zolo-sDateTime-color);
    }

    &.zolo-post-style-1 .zolo-post-category a {
        color: var(--zolo-category-color);
    }

    &.zolo-post-style-2 .zolo-post-content,
    &.zolo-post-style-3 .zolo-post-content {
        --zolo-content-padding: 30px;
        position: absolute;
        bottom: 0;
        left: 0;
        padding: var(--zolo-content-padding);
        z-index: 1;
    }

    &.zolo-post-style-2 .zolo-post-meta-box {
        top: 0;
        opacity: 0;
        transform: translateY(-6%);
        background: transparent;
    }

    &.zolo-post-style-2 .zolo-post-image:before {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(45deg, rgba(7, 4, 34, 0.69), transparent);
        z-index: 1;
        pointer-events: none;
    }

    &.zolo-post-style-2 .zolo-post-category a,
    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-category a {
        background-color: rgba(236, 235, 254, 0.28);
    }

    &.zolo-post-style-2 .zolo-post-category a {
        --zolo-category-color: #fff;
        color: var(--zolo-category-color);
    }

    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-category a {
        --zolo-category-hover-color: #fff;
        color: var(--zolo-category-hover-color);
    }

    &.zolo-post-style-2 .zolo-post-dateTime,
    &.zolo-post-style-2 .zolo-post-desc p,
    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-dateTime,
    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-desc p,
    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-author-name a {
        color: #e5e6ed;
    }

    &.zolo-post-style-2 .zolo-post-title a,
    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-title a {
        color: #e5e6ed;
    }

    &.zolo-post-style-3 .zolo-post-image:before,
    &.zolo-post-style-3 .zolo-post-image:after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        pointer-events: none;
        transition:
            background cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s,
            opacity cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
    }

    &.zolo-post-style-3 .zolo-post-image:before {
        background: linear-gradient(45deg, rgba(7, 4, 34, 0.69), transparent);
    }

    &.zolo-post-style-3 .zolo-post-image::after {
        opacity: 1;
        background-color: rgba(255, 255, 255, 0.62);
        backdrop-filter: contrast(7) blur(15px);
    }

    .zolo-post-dateTime,
    .zolo-post-desc p {
        transition: color cubic-bezier(0.645, 0.045, 0.355, 1) 0.35s;
    }

    &.zolo-post-style-3 .zolo-post-item:hover .zolo-post-image::after {
        opacity: 0;
        background: transparent;
    }

    &.zolo-post-style-3 .zolo-post-meta-box {
        top: 0;
        opacity: 1;
        background: transparent;
        z-index: 9;
    }

    &.zolo-post-style-3 .zolo-post-author-name,
    &.zolo-post-style-3 .zolo-post-author-name a {
        --zolo-author-name-color: #111013;
        color: var(--zolo-author-name-color);
    }

    &.zolo-post-style-3 .zolo-post-category a {
        --zolo-category-color: #111013;
        color: var(--zolo-category-color);
        background-color: #d7d8dd61;
    }

    // navigation css start
    &.zolo-post-style-1 {
        .swiper-nav-button {
            top: 40%;
        }
    }

    .swiper-pagination-bullet {
        margin: 0 !important;
    }

    .swiper-pagination {
        --zolo-pagination-offset: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: var(--zolo-pagination-gap);
        z-index: 11;
        bottom: 0 !important;
        transform: translateY(var(--zolo-pagination-offset));
        .swiper-pagination-bullet {
            background-color: transparent;
            width: var(--zolo-pagination-size);
            height: var(--zolo-pagination-size);
            border-width: var(--zolo-pagination-border-width);
            border-style: var(--zolo-pagination-border-style);
            border-color: var(--zolo-pagination-border-color);
            transition: all 0.3s ease;

            &.swiper-pagination-bullet-active {
                transform: scale(1.3);
                background-color: var(--zoloblocks-brand-color);
                outline: none;
            }
        }
    }

    .swiper-nav-button {
        position: absolute;
        top: 50%;
        z-index: 1;
        transform: translateY(-50%);
        width: var(--zolo-nav-button-size);
        height: var(--zolo-nav-button-size);
        color: var(--zolo-nav-button-color);
        fill: var(--zolo-nav-button-color);
        background-color: #fff;
        border-radius: var(--zolo-nav-button-border-radius);
        transition: all 0.3s ease;
        &:hover {
            fill: var(--zoloblocks-brand-button-color);
            color: var(--zoloblocks-brand-button-color);
            background-color: var(--zoloblocks-brand-color);
        }

        &:after {
            font-size: clamp(0.625rem, 1.5vw, 0.875rem);
        }
        &.swiper-zolo-next,
        &.swiper-button-next {
            right: var(--zolo-nav-button-right);
        }
        &.swiper-zolo-prev,
        &.swiper-button-prev {
            left: var(--zolo-nav-button-left);
        }
    }

    // custom navigation icon
    .swiper-zolo-prev {
        .zolo__display-icon {
            display: inline-block;
        }
    }

    .swiper-zolo-prev,
    .swiper-zolo-next {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        svg {
            width: var(--zolo-nav-button-icon-size);
            height: var(--zolo-nav-button-icon-size);
            display: block;
        }
    }

    .swiper-button-next:after,
    .swiper-rtl .swiper-button-prev:after {
        content: 'next';
    }

    .swiper-button-prev:after,
    .swiper-rtl .swiper-button-next:after {
        content: 'prev';
    }
}
// pagination css  start
.zolo-pagination-nav {
    display: inline-flex;
    align-items: center;
    border-radius: 5px;
    overflow: hidden;
    .page-numbers {
        transition: 0.2s;
        font-size: 14px;
        padding: 8px 15px;
        text-transform: uppercase;
        text-decoration: none;
        background: #f0f0f4;
        cursor: pointer;
        + .page-numbers {
            border-left: 1px solid #d9d9d9;
        }
        &.current {
            background-color: #c5c5c5;
        }
    }
}
// pagination css  end
